.container_fluid_custom { max-width: 1330px; }

html, body { height: 100%; }

.wrapper { display: flex; flex-direction: column; height: 100%; }

.w1 { flex: 1 0 auto; padding-bottom: 30px; }

.nano.has-scrollbar .message_row { width: calc(100% - 10px); }

.nano.has-scrollbar .message_row .ico { right: 12px; }

.nano > .nano-pane { width: 4px; background: none; right: 3px; }

.nano > .nano-pane > .nano-slider { margin: 0; background: #ccd2dc; }

.footer { flex: 0 0 auto; border-top: 4px solid #26bcf1; padding: 28px 0; }

.footer_logo { color: #f8f8f8; margin-right: 20px; }

.footer_logo i { font-weight: bold; font-style: normal; color: #26bcf1; }

.footer_logo:hover { text-decoration: none; color: #f8f8f8; }

.logo_block { margin-right: auto; }

.logo_block .copy { color: #909ba1; position: relative; padding-left: 20px; padding-right: 44px; }

.logo_block .copy:before { content: ''; position: absolute; top: 50%; transform: translateY(-50%); left: 0; width: 1px; height: 25px; background: #46494c; }

.footer_nav { margin: 0; padding: 0; list-style: none; font-size: 14px; }

.footer_nav li { position: relative; padding-left: 20px; padding-right: 30px; }

.footer_nav li:last-child { padding-right: 0; }

.footer_nav li:before { content: ''; width: 6px; height: 6px; border-radius: 50%; background: #909ba1; top: 50%; transform: translateY(-50%); left: 0; position: absolute; }

.footer_nav li.active a { text-decoration: underline; color: #26bcf1; }

.footer_nav li.active:before { background: #26bcf1; }

.footer_nav li a { color: #909ba1; }

.footer_nav li a:hover { color: #26bcf1; text-decoration: none; }

.etc_inf { margin: 0; padding: 0 0 0 40px; list-style: none; font-size: 14px; }

.etc_inf li { padding: 6px 0; }

.etc_inf i, .etc_inf span { display: inline-block; vertical-align: middle; }

.etc_inf span { padding-left: 10px; }

.etc_inf a { color: #fff; }

.etc_inf a:hover { text-decoration: none; }

.lang { text-transform: uppercase; }

.lang .btn { background: none; border: 0; padding: 0 0 0 18px; }

.lang .btn:after { content: none; }

.lang .btn:before { content: ''; display: inline-block; width: 0; height: 0; position: absolute; top: 50%; left: 0; transform: translateY(-50%); border-top: 6px solid #f8f8f8; border-right: 6px solid transparent; border-bottom: 0; border-left: 6px solid transparent; }

.lang .btn:not(:disabled):not(.disabled):active, .lang .btn:not(:disabled):not(.disabled).active, .lang .show > .btn.dropdown-toggle { background: none !important; border: 0; }

.lang .dropdown-menu { min-width: 38px; padding: 0; border-radius: 3px; }

.lang .dropdown-menu a { padding-left: 0; padding-right: 0; text-align: center; }

.lang .dropdown-menu a:first-of-type { border-top-left-radius: 3px; border-top-right-radius: 3px; }

.lang .dropdown-menu a:last-of-type { border-bottom-left-radius: 3px; border-bottom-right-radius: 3px; }

.header a, .header a * { transition: color 0.3s ease ,  background 0.3s ease; }

.header a:hover, .header a *:hover { transition: color 0.5s,  ease background 0.5s ease; }

.header .parts_btn { padding: 0 20px; }

.header .parts_btn a { min-width: 120px; text-align: center; }

.header .parts_btn a:hover i { color: #fff; }

.header .parts_btn a + a { margin-left: 10px; }

.header .parts_btn i, .header .parts_btn span { display: inline-block; vertical-align: middle; }

.header .parts_btn i { font-size: 14px; }

.header .parts_btn span { color: #fff; padding-left: 6px; }

@media (max-width: 1199.98px) { .header .parts_btn a { min-width: 50px; } }

@media (max-width: 991.98px) { .header .parts_btn { width: 100%; flex-wrap: wrap; padding: 10px 30px 20px; }
  .header .parts_btn a { width: 100%; margin-top: 20px; height: 50px; display: flex; align-items: center; justify-content: center; }
  .header .parts_btn a i { font-size: 18px; }
  .header .parts_btn a span { font-size: 18px; font-weight: 500; }
  .header .parts_btn a + a { margin: 20px 0 0; } }

@media (max-width: 991.98px) { .header { height: 80px; }
  .header > .container_fluid_custom { height: 100%; position: relative; }
  .header .logo { position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); } }

.btn_mob { position: absolute; top: 50%; right: 30px; transform: translateY(-50%); cursor: pointer; }

.btn_mob i { font-size: 20px; color: #f8f8f8; }

.message_box_in-mobile { position: absolute !important; top: 50%; left: 30px; transform: translateY(-50%); }

.message_box_in-mobile .message_link { font-size: 24px; color: #f8f8f8; }

.message_box_in-mobile .message_link:hover { text-decoration: none; }

.message_box_in-mobile .message_link .number { height: 20px !important; min-width: 20px !important; font-size: 14px !important; line-height: 18px !important; }

.message_box_in-mobile .message_drop { position: fixed !important; background: red !important; border: 10px solid #000 !important; width: 100% !important; height: 100% !important; top: 80px !important; right: auto !important; left: 0; opacity: 1 !important; }

.message_box_in-mobile .message_drop .nano { max-height: 100% !important; min-height: 100% !important; }

.bar_menu { flex: 1 1 auto; width: 1%; }

@media (max-width: 991.98px) { .bar_menu { background: red; position: fixed; top: 80px; left: -100%; height: calc(100% - 80px); min-height: calc(100% - 80px); overflow: auto; width: 300px; z-index: 200; transition: all 0.3s ease; background: #343537; flex-wrap: wrap !important; align-content: flex-start !important; }
  .bar_menu.opened { left: 0; } }

.main_nav { margin: 0; padding: 0 0 0 60px; list-style: none; height: 70px; }

.main_nav a { font-size: 14px; padding: 0 10px; position: relative; display: flex; height: 100%; justify-content: center; align-items: center; }

.main_nav a:hover { text-decoration: none; }

.main_nav a:hover * { color: #f4f4f4; }

.main_nav a:hover:after { content: ''; width: 100%; height: 4px; position: absolute; bottom: 0; left: 0; background: #04cbf8; }

.main_nav li { margin-right: 20px; }

.main_nav li:last-of-type { margin-right: 0; }

.main_nav li.active a * { color: #f4f4f4; }

.main_nav li.active a:after { content: ''; width: 100%; height: 4px; position: absolute; bottom: 0; left: 0; background: #04cbf8; }

.main_nav i, .main_nav span { color: #757c82; }

.main_nav span { margin-left: 10px; }

@media (max-width: 1199.98px) { .main_nav { padding-left: 30px; }
  .main_nav li { margin: 0; } }

@media (max-width: 991.98px) { .main_nav { padding: 0; display: block !important; width: 100%; height: auto; border-top: 1px solid #434548; }
  .main_nav li { border-bottom: 1px solid #434548; }
  .main_nav li.active a:after { width: 7px; height: 100%; }
  .main_nav li a { height: 80px; padding-left: 30px; font-size: 18px; font-weight: 500; justify-content: flex-start; }
  .main_nav li a:hover:after { width: 7px; height: 100%; } }

.menu_overlay { width: 100%; height: 100%; position: fixed; top: 0; left: 0; z-index: 2; background: rgba(0, 0, 0, 0.4); display: none; }

.menu_overlay.opened { display: block; }

.balance_block { padding: 0 20px; position: relative; }

.balance_block a { font-size: 25px; color: #757c82; }

.balance_block a:hover { text-decoration: none; }

.balance_block:after, .balance_block:before { content: ''; width: 1px; height: 20px; position: absolute; top: 50%; left: 0; background: #424345; transform: translateY(-50%); }

.balance_block:after { left: auto; right: 0; }

.balance_block span, .balance_block em { font-style: normal; color: #757c82; }

.balance_block em { margin-right: auto; padding-right: 10px; }

.balance_block .balance_list { margin: 0; padding: 0 0 0 15px; list-style: none; }

.balance_block strong { font-weight: 500; }

@media (max-width: 991.98px) { .balance_block { padding: 25px 30px 30px; width: 100%; justify-content: space-between !important; }
  .balance_block span { display: none !important; }
  .balance_block ul { padding-left: 0 !important; font-size: 18px; line-height: 21px; }
  .balance_block:after { content: none !important; } }

.etc_func { color: #757c82; margin: 0; padding: 0; list-style: none; }

.etc_func li { padding-left: 20px; }

.etc_func i { font-size: 16px; display: inline-block; vertical-align: middle; }

.etc_func span { display: inline-block; vertical-align: middle; padding-left: 7px; }

.etc_func a { color: #757c82; }

.etc_func a:hover { text-decoration: none; color: #f4f4f4; }

@media (max-width: 1199.98px) { .etc_func .log_out span { display: none; } }

.message_box { position: relative; }

.message_box .message_link { position: relative; display: block; }

.message_box .message_link.active { color: #f4f4f4; }

.message_box .message_link .number { position: absolute; top: 0; right: 0; transform: translate(50%, -25%); font-size: 10px; color: #343537; font-style: normal; font-weight: bold; height: 16px; line-height: 12px; background: #26bcf1; border-radius: 50%; min-width: 16px; padding: 0 3px; text-align: center; border: 2px solid rgba(38, 188, 241, 0.45); background-clip: padding-box; }

.log_out { white-space: nowrap; }

.message_drop { position: absolute; top: 35px; right: -42px; width: 270px; background: #fff; border: 1px solid #ededed; border-radius: 5px; box-shadow: 0 7px 10px 0px rgba(0, 0, 1, 0.03); opacity: 0; z-index: 100; }

.message_drop.opened { opacity: 1; }

.message_drop:after { content: ''; width: 0; height: 0; top: -8px; right: 40px; position: absolute; border-bottom: 8px solid #fff; border-left: 8px solid transparent; border-right: 8px solid transparent; }

.message_drop .nano { max-height: 300px; height: 100%; min-height: 100px; }

.message_drop .message_header { padding: 10px 20px 13px; border-bottom: 1px solid #ccd2dc; position: relative; margin-bottom: 5px; }

.message_drop .message_header h3 { margin: 0; font-size: 14px; font-weight: bold; color: #e25757; padding-right: 17px; }

.message_drop .message_header h3 span { display: inline-block; vertical-align: middle; padding: 0; }

.message_drop .message_header .ico { position: absolute; top: 15px; right: 20px; font-size: 10px; color: #757c82; }

.message_drop .message_row { padding: 12px 20px 10px; border-top: 1px solid #ccd2dc; position: relative; }

.message_drop .message_row:first-of-type { border-top: 0; }

.message_drop .message_row .ico { position: absolute; top: 14px; right: 20px; font-size: 8px; color: #babdc0; }

.message_drop .message_row h4 { margin: 0; font-weight: bold; color: #424951; font-size: 12px; padding-bottom: 5px; padding-right: 15px; }

.message_drop .message_row p { margin: 0; font-size: 12px; color: #757c82; line-height: 14px; padding-bottom: 3px; }

.message_drop .message_row .read_more { color: #26bcf1; text-decoration: underline; font-weight: bold; }

.message_drop .message_row .read_more:hover { text-decoration: none; }

.message_drop .message_row .inf { display: block; padding: 3px 0 0 0; font-weight: bold; color: #424951; }

.message_drop .message_row .inf em { font-style: normal; }
